import api from "@/store/api"
import utils from '@/utils'

export default {
    name: 'organizations',
    actions: {
        async loadOrganizations({commit}) {
            await api.loadObjects(
                "GetOrganizations",
                "/dictionaries/organizations",
                commit,
                "setOrganizations"
            );
        }
    },
    mutations: {
        setOrganizations(state, organizations) {
            state.organizations = organizations;
        }
    },
    state: {
        organizations: [],
    },
    getters:{
        getOrganizations: (state) => {
            return state.organizations;
        },
        getOrganizationByID: (state) => (ID) => {
            return utils.getItemByID(state.organizations, ID);
        },
    }
}
