import api from "@/store/api"
import utils from '@/utils'

export default {
    name: 'fields',
    actions: {
        async loadFields({commit}) {
            await api.loadObjects(
                "GetFields",
                "/dictionaries/fields",
                commit,
                "setFields"
            );
        }
    },
    mutations: {
        setFields(state, fields) {
            state.fields = fields;
        }
    },
    state: {
        fields: [],
    },
    getters:{
        getFields: (state) => {
            return state.fields;
        },
        getFieldByID: (state) => (ID) => {
            return utils.getItemByID(state.fields, ID);
        },
    }
}
