import api from "@/store/api"
import utils from '@/utils'

export default {
    name: 'complexes',
    actions: {
        async loadComplexes({commit}, projectId) {
            const params = projectId ? ("?projectId=" + projectId) : "";
            await api.loadObjects(
                "GetComplexes",
                "/dictionaries/complexes" + params,
                commit,
                "setComplexes"
            );
        }
    },
    mutations: {
        setComplexes(state, complexes) {
            state.complexes = complexes;
        }
    },
    state: {
        complexes: [],
    },
    getters:{
        getComplexes: (state) => {
            return state.complexes;
        },
        getComplexByID: (state) => (ID) => {
            return utils.getItemByID(state.complexes, ID);
        },
    }
}
