import api from "@/store/api"
//import utils from '@/utils'

export default {
    name: 'prComDoc',
    actions: {
        async loadContracts({commit}) {
            await api.loadObjects(
                "GetProjects",
                "/api/contract/",
                commit,
                "setContracts"
            );
        },
        async loadComplects({commit}, id_contract, id_stage) {
            await api.loadObjects(
                "getComplects",
                "/sapsan/complects"+(id_contract? ("?id_contract=" + id_contract) : "")+(id_stage? ("?id_stage=" + id_stage) : ""),
                commit,
                "setComplects"
            );
        },
        async loadDocuments({commit}, id_complect) {
            await api.loadObjects(
                "getDocuments",
                "/sapsan/documents"+(id_complect? ("?id_complect=" + id_complect) : ""),
                commit,
                "setDocuments"
            );
        },
        async loadStage({commit}, id_contract) {
            await api.loadObjects(
                "getStage",
                "/sapsan/stage"+(id_contract? ("?id_contract=" + id_contract) : ""),
                commit,
                "setStage"
            );
        }
    },
    mutations: {
        setContracts(state, contracts) {
            
           
                
                state.contracts = contracts
                //console.log( state.contracts)
        },
        setComplects(state, complects) {
            state.complects = complects
             console.log( state.complects)
        },
        setDocuments(state, documents) {
            state.documents=documents;
        },
        setStage(state, stage) {
            state.stage=stage;
        }
        
    },
    state: {
        contracts: [],
        complects: [],
        documents: [],
        stage: []
    },
    getters:{
        getContracts: (state) => {

            return state.contracts;
        },
        getComplects: (state) => {
            return state.complects;
        },
        getDocuments: (state) => {
            return state.documents;
        },
        getStage: (state) => {
            return state.stage;
        },
    }
}